import {
	login
} from '../../api/login.js';
import {
	setToken,
	setUserInfo
} from '@/utils/auth.js';
import {
	goPage
} from '@/utils/index.js';
import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			phone: '',
			password: '',
			loading: false
		}
	},
	computed: {
		...mapState(['routerWait'])
	},
	methods: {
		register() {
			uni.navigateTo({
				url: '/pages/register/register'
			});
		},
		changePhone(e) {
			this.phone = e.detail.replace(/\s/g, '');
		},
		changePassword(e) {
			this.password = e.detail.replace(/\s/g, '');
		},
		getCode() {
			getAuthCode(this.phone);
		},
		phoneLogin() {
			let phone = this.phone;
			let password = this.password;
			this.loading = true;
			login({
				username: phone,
				password
			}).then(res => {
				if (res.code == 200) {
					uni.showToast({
						icon: 'none',
						title: '登录成功'
					})
					setToken(res.data.token);
					setUserInfo(res.data.userInfo);

					if (this.routerWait.routerPath) {
						goPage(this.routerWait.routerType, this.routerWait.routerPath);
					}
				} else {
					uni.showToast({
						icon: 'none',
						title: res.message
					})
				}
				this.loading = false;
			}).catch(() => {
				this.loading = false;
			});

		},
		wxLogin() {
			uni.navigateBack();
		}
	}
}
